<template>
  <div id="historypurchase">
    <h3 style="text-align: center; color: red"></h3>
    <el-table
      ref="multipleTable"
      :data="orderlist"
      border
      tooltip-effect="dark"
      :header-cell-style="{ background: '#f7f7f7' }"
      style="width: 1010px; margin: 0px auto"
    >
      <el-table-column prop="orderid" label="" width="200">
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column prop="name" label="" width="200">
      </el-table-column>
      <el-table-column prop="status" label="" width="200">
      </el-table-column>
      <el-table-column label="" width="210">
        <template slot-scope="scope">
          <el-button size="mini" @click="listdetail(scope.row)"
            ></el-button
          >
          <el-button size="mini" @click="handleOrder(scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      title=""
      :visible.sync="detailpagevisible"
      :modal-append-to-body="false"
    >
      <el-table
        ref="multipleTable"
        :data="detaillist"
        border
        tooltip-effect="dark"
        :header-cell-style="{ background: '#f7f7f7' }"
        style="width: 602px; margin: 10px auto"
      >
        <el-table-column prop="id" label="" width="150">
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">{{ scope.row.name }}</template>
        </el-table-column>
        <el-table-column prop="price" label="" width="150">
        </el-table-column>
        <el-table-column prop="num" label="" width="150">
        </el-table-column>
      </el-table>
      <div slot="footer">
        <el-button @click="detailpagevisible = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "historypurchase",
  data() {
    return {
      orderlist: [
        {
          orderid: 1,
          date: "2016-05-05",
          name: "yyang",
          status: "",
        },
        {
          orderid: 2,
          date: "2016-06-03",
          name: "yyang",
          status: "",
        },
        {
          orderid: 3,
          date: "2016-07-09",
          name: "yyang",
          status: "",
        },
        {
          orderid: 4,
          date: "2016-12-03",
          name: "yyang",
          status: "",
        },
        {
          orderid: 5,
          date: "2018-02-03",
          name: "yyang",
          status: "",
        },
      ],
      detaillist: [
        { id: 1, name: "", price: "99.00", num: 100 },
        { id: 2, name: "", price: "179.00", num: 100 },
        { id: 3, name: "", price: "259.00", num: 100 },
        { id: 4, name: "", price: "939.00", num: 100 },
        { id: 5, name: "", price: "59.00", num: 100 },
      ],
      detailpagevisible: false,
    };
  },
  methods: {
    listdetail() {
      this.detailpagevisible = true;
    },
    handleOrder(row) {
      this.$router.push({
        path: `/order/details/${row.orderid}`,
      });
    },
  },
};
</script>

